@import url(https://fonts.googleapis.com/css2?family=Arvo&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;700&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
    display: block;
}
body {
    line-height: 1;
}
ol, ul {
    list-style: none;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
:root {
    /* Colours */
    --mediumPurple: #8A3385;
    --darkPurple: #220044;
    --lightPurple: #A160A4;
    --gold: #EFAF2E;
    --cream: #FAE7CF;

    /* Padding */
    --smallPadding: 1rem;
    --mediumPadding: 2rem;
    --largePadding: 3rem;
    --hugePadding: 4rem;

    /* Font size */
    --normal: 1rem;
    --big: 2rem;
    --biggest: 2.5rem; 
}
/* Import fonts */ /* Arvo */ /* Raleway */

/* Aurabesh fonts */
@font-face {
    font-family: 'Aurebesh';
    src: url(/static/media/Aurebesh.aee485af.otf);
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Aurebesh';
    src: url("/static/media/Aurebesh Italic.0502d94d.otf");
    font-weight: normal;
    font-style: italic;
}

@font-face {
    font-family: 'Aurebesh';
    src: url("/static/media/Aurebesh Bold.bee81cf0.otf");
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'Aurebesh';
    src: url("/static/media/Aurebesh Bold Italic.7c76514a.otf");
    font-weight: bold;
    font-style: italic;
}

h1, h2, h3 {
    font-family: 'Arvo', serif;
}

h1:hover, h2:hover, h3:hover, p:hover, a:hover, li:hover, button:hover {
