    padding: var(--smallPadding) 0 0 0;
}

/* REACTIVENESS */
@media screen and (max-width: 720px) {
    .join_container {
        width: 90%;
        min-width: 300px;
    }
}
.menu_links {
    height: 90vh;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;

    padding: var(--mediumPadding);

    background-color: var(--darkPurple);
}

.menu_link {
    text-decoration: none;
    color: var(--gold);
}



